@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@400;500;800&family=Lato&display=swap);
body {
  margin: 0;
  font-family: 'Darker Grotesque', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #361914;
}

/* Typography */
h1 {
  font-size: 56px;
  font-weight: 400;
  margin-top: 30px;
}

h2 {
  font-size: 28px;
  font-weight: 500;
  margin-top: 20px;
  text-transform: capitalize;
}

h3 {
  font-size: 16px;
  font-weight: 800;
  letter-spacing: 2px;
  text-transform: uppercase;
}

h4 {
  font-family: 'lato', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 18px;
  /* font-style: italic; */
  font-weight: 400;
  color: #dad524;
  border: 1px rgba(218, 213, 36, .50) solid;
  text-align: center;
  padding: 20px;
  margin: 30px;
}

p, 
a {
  font-family: 'lato', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

a:hover {
  text-decoration: none;
  color: #4CE0D2;
  transition: ease .5s;
}

/* Colors */
.bg-color {
  background-color: #361914;
}
.bg-lighter-color {
  background-color: #7c3c30;
}
.bg-lightest-color {
  background-color: #b85644;
}
.text-color {
  color:#dae8ec;
}
.interactive-color {
  color: #dae8ec;
}


@media only screen and (max-width: 768px) {
  h1 {
    font-size: 46px;
  }
}

.hero-image {
   background: 
      linear-gradient(180deg, #b85644c0 0%, #361914 100%), 
      url(/react-employee-directory/static/media/hero-image.f3f5c86d.jpg); 
   background-size: cover;
   height: 320px;
   padding-top: 35px;
}

@media only screen and (max-width: 768px) {
   .hero-image {
      height: 230px;
   }  
}  
@media only screen and (max-width: 768px) {
  .button-group {
    display: flex;
    justify-content: center;
  }
}

.margin {
  margin-bottom: 20px;
}

.form-control {
  padding: 22px 0 22px 2.5rem;
}

.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  top: 16px;
  left: 32px;
  font-size: 14px;
  text-align: center;
  pointer-events: none;
  color: #dae8ec;
}

.search-input {
  font-family: 'lato', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #dae8ec;
  background-color: #b85644;
  border: none;
}

.search-input::-webkit-input-placeholder {
  color: #acbabe;
}

.search-input:-ms-input-placeholder {
  color: #acbabe;
}

.search-input::-ms-input-placeholder {
  color: #acbabe;
}

.search-input::placeholder {
  color: #acbabe;
}

.search-input {
  border-radius: 50px;
}

.search-input:focus  {
  background-color: #7c3c30;
  color: #dae8ec; 
}




.dropdown, 
.btn {
  font-family: 'lato', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #dae8ec;
  background-color: #b85644;
  border: none;
}

.btn {
  padding: .65rem 1rem;
}

.border-radius {
  border-radius: 0 10px 10px 0;
}

.btn:hover {
  background-color: #7c3c30;
  color: #dae8ec;
}
.btn:focus {
  background-color: #7c3c30;
  color: #dae8ec;
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus {
  color: #7c3c30;
  background-color: #dae8ec;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle:active {
  color: #7c3c30;
  background-color: #dae8ec;
}

.margin {
  margin-bottom: 20px;
}

.dropdown-menu {
  background-color: #b85644;
  transition: ease .2s;
}

.dropdown-item {
  color: #dae8ec;
}

.dropdown-item:hover {
  background-color: #7c3c30;
  color: #dae8ec;
}


@media only screen and (max-width: 768px) {
  
  .full-width {
    width: 100%;
  }

}

.wrapper {
  padding-top: 25px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.card-container {
  background: #7c3c30;
  border-radius: 18px;
  box-shadow: 0px 4px 20px 0px rgba(0,0,0,0.8);
  margin-bottom: 20px;
  width: 265px;
}

.card-intro {
  text-align: center;
  padding: 50px 10px 20px;
}

.card-intro img {
  border-radius: 10px;
}

hr {
  border-top: 3px solid rgba(0,0,0,.1);
}


.card-contact {
  padding: 15px 0 20px;
}

li {
  color: #dae8ec;
  list-style: none;
  margin-bottom: 15px;
}

a {
  padding-left: 10px;
  color: #dae8ec;
}

